import React from 'react';
import ReactDOM from 'react-dom';
import '../../../../components/style.scss';

var A = 0;
var B = 0;
var C = 0;
var D = 0;
var E = 0;
var F = 0;
var G = 0;
var H = 0;
var I = 0;
var J = 0;
var K = 0;
var L = 0;
var N = 0;

class Input extends React.Component {

  constructor (){
    super ();
    this.state={
      num1:'',
      total:'',
      haematuria: 0,
      currentSmoker:0,
      exSmoker:0,
      famHist:0,
      male:0,
      benignIx:0,
      voiding:0,
      storage:0,
      mixed:0,
      uti:0,
      catheter:0,
      pelvicXRT:0,
      singleuti:0,
      text:'',
    }

  }

handlenum1 = (event) => {
  this.setState({
    num1:event.target.value
  })
}

handlehaematuria = (event) => {
A=A+1;
if (A%2==0) {
this.state.haematuria = 0;} else {
this.state.haematuria = 1;
}
}

handlecurrentSmoker = (event) => {
B=B+1;
if (B%2==0) {
this.state.currentSmoker = 0;} else {
this.state.currentSmoker = 1;
}
}

handleexSmoker = (event) => {
C=C+1;
if (C%2==0) {
this.state.exSmoker = 0;} else {
this.state.exSmoker = 1;
}
}

handlefamHist = (event) => {
D=D+1;
if (D%2==0) {
this.state.famHist = 0;} else {
this.state.famHist = 1;
}
}

handlemale = (event) => {
E=E+1;
if (E%2==0) {
this.state.male = 0;} else {
this.state.male = 1;
}
}

handlebenignIx = (event) => {
F=F+1;
if (F%2==0) {
this.state.benignIx = 0;} else {
this.state.benignIx = 1;
}
}

handlevoiding = (event) => {
G=G+1;
if (G%2==0) {
this.state.voiding = 0;} else {
this.state.voiding = 1;
}
}

handlestorage = (event) => {
H=H+1;
if (H%2==0) {
this.state.storage = 0;} else {
this.state.storage = 1;
}
}

handlemixed = (event) => {
I=I+1;
if (I%2==0) {
this.state.mixed = 0;} else {
this.state.mixed = 1;
}
}

handleuti = (event) => {
J=J+1;
if (J%2==0) {
this.state.uti = 0;} else {
this.state.uti = 1;
}
}

handlecatheter = (event) => {
K=K+1;
if (K%2==0) {
this.state.catheter = 0;} else {
this.state.catheter = 1;
}
}

handlepelvicXRT = (event) => {
L=L+1;
if (L%2==0) {
this.state.pelvicXRT = 0;} else {
this.state.pelvicXRT = 1;
}
}


handlesingleuti= (event) => {
N=N+1;
if (N%2==0) {
this.state.singleuti = 0;} else {
this.state.singleuti= 1;
}
}

exe = (event) =>{
  event.preventDefault();
  this.setState({total: (9 + 10*[(2.23*(this.state.haematuria)) + (0.99*(this.state.male)) + (0.08*((parseInt(this.state.num1))-64.3))
  + (1.25*(this.state.currentSmoker)) + (0.68*(this.state.exSmoker)) + (0.77*(this.state.famHist))
  - (0.71*(this.state.benignIx)) - (0.55*(this.state.voiding)) - (0.35*(this.state.storage)) - (0.48*(this.state.mixed )) - (0.92*(this.state.singleuti)) - (0.86*(this.state.uti))
  - (1.24*(this.state.catheter)) - (0.51*(this.state.pelvicXRT)) - (0.81*(this.state.haematuria)*(this.state.male)) - (0.02*(this.state.haematuria)*((parseInt(this.state.num1))-64.3))
  - (0.03*(this.state.male)*((parseInt(this.state.num1))-64.3))]
).toFixed(0)});
}


  render() {
    if (this.state.total < 0){
            this.state.total = 0.01;
          }

    let inputStyle = {
      color: "black"
    };


    if (this.state.total >= 56 && this.state.total <=76){
            inputStyle = {color: "#e27300"};
            this.state.text = " (There is an INTERMEDIATE Risk of Urinary Tract Cancer)";
          } else if (this.state.total > 76) {
            inputStyle = {color: "red"};
            this.state.text = "(There is a HIGH Risk of Urinary Tract Cancer)";
          } else if (this.state.total >0 && this.state.total <56) {
            this.state.text = "(There is a LOW Risk of Urinary Tract Cancer)";
          } else {
            this.state.text = "";
          }

    return (
    <div class="container">
      <div class="field">
        <form onSubmit={this.exe}>
        <div class="control">
          <h1 className="title is-size-5 is-size-4-mobile">Age</h1>
          <input class="input is-primary" type="text" value={this.state.num1} onChange={this.handlenum1} placeholder = "Input Age"></input>
        </div>
        <br></br>
        <div class="field">
          <input id="haematuria" type="checkbox" name="switchExample" class="switch is-rtl" onChange={this.handlehaematuria}></input>
            <label for="haematuria" className="is-size-5 is-size-3-mobile"><strong> Visible Haematuria </strong></label>
        </div>
        <div class="field">
          <input id="currentSmoker" type="checkbox" name="switchExample" class="switch is-rtl" onChange={this.handlecurrentSmoker}></input>
            <label for="currentSmoker" className="is-size-5 is-size-3-mobile"><strong> Current Smoker </strong></label>
        </div>
        <div class="field">
          <input id="exSmoker" type="checkbox" name="switchExample" class="switch is-rtl" onChange={this.handleexSmoker}></input>
            <label for="exSmoker" className="is-size-5 is-size-3-mobile"><strong> Ex-Smoker </strong></label>
        </div>
        <div class="field">
          <input id="famHist" type="checkbox" name="switchExample" class="switch is-rtl" onChange={this.handlefamHist}></input>
            <label for="famHist" className="is-size-5 is-size-3-mobile"><strong> Family History of Urothelial Cancer </strong></label>
        </div>
        <div class="field">
          <input id="male" type="checkbox" name="switchExample" class="switch is-rtl" onChange={this.handlemale}></input>
            <label for="male" className="is-size-5 is-size-3-mobile"><strong> Male </strong></label>
        </div>
        <div class="field">
          <input id="benignIx" type="checkbox" name="switchExample" class="switch is-rtl" onChange={this.handlebenignIx}></input>
            <label for="benignIx" className="is-size-5 is-size-3-mobile"><strong> Previous benign haematuria investigations </strong></label>
        </div>
        <div class="field">
          <input id="voiding" type="checkbox" name="switchExample" class="switch is-rtl" onChange={this.handlevoiding}></input>
            <label for="voiding" className="is-size-5 is-size-3-mobile"><strong> LUTS: Voiding/Obstructive </strong></label>
        </div>
        <div class="field">
          <input id="storage" type="checkbox" name="switchExample" class="switch is-rtl" onChange={this.handlestorage}></input>
            <label for="storage" className="is-size-5 is-size-3-mobile"><strong> LUTS: Storage/Irritative </strong></label>
        </div>
        <div class="field">
          <input id="mixed" type="checkbox" name="switchExample" class="switch is-rtl" onChange={this.handlemixed}></input>
            <label for="mixed" className="is-size-5 is-size-3-mobile"><strong> LUTS: Mixed </strong></label>
        </div>
        <div class="field">
          <input id="singleuti" type="checkbox" name="switchExample" class="switch is-rtl" onChange={this.handlesingleuti}></input>
            <label for="singleuti" className="is-size-5 is-size-3-mobile"><strong> Single UTI associated with haematuria </strong></label>
        </div>
        <div class="field">
          <input id="uti" type="checkbox" name="switchExample" class="switch is-rtl" onChange={this.handleuti}></input>
            <label for="uti" className="is-size-5 is-size-3-mobile"><strong> Recurrent UTI associated with haematuria </strong></label>
        </div>
        <div class="field">
          <input id="catheter" type="checkbox" name="switchExample" class="switch is-rtl" onChange={this.handlecatheter}></input>
            <label for="catheter" className="is-size-5 is-size-3-mobile"><strong> Catheter? (indweling or ISC) </strong></label>
        </div>
        <div class="field">
          <input id="pelvicXRT" type="checkbox" name="switchExample" class="switch is-rtl" onChange={this.handlepelvicXRT}></input>
            <label for="pelvicXRT" className="is-size-5 is-size-3-mobile"><strong> Previous pelvic radiotherapy </strong></label>
        </div>
        <br></br>
        <button class="button is-info is-size-5 is-size-3-mobile" type="submit"><strong>Calculate Risk of Urinary Tract Cancer</strong></button>
        </form>
        <br></br>
        <h1 className="title is-size-3-mobile" style={inputStyle} > {this.state.total} {this.state.text} </h1>
        <br></br>
        <h1 className="title is-size-3-mobile notification is-danger is-light "> Consider an increased risk of upper tract cancer if the patient has flank pain and haematuria. </h1>
      </div>
    </div>
    );
  }
}



export default Input
